import React, { useState, useEffect } from 'react';
import { useMsal } from '@azure/msal-react';
import { loginRequest } from '../authConfig';
import '../styles/components/UserManagement.css';

function UserManagement() {
  const { instance, accounts } = useMsal();
  const [users, setUsers] = useState([]);
  const [roles, setRoles] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    loadUsers();
    loadRoles();
  }, []);

  const getAccessToken = async () => {
    try {
      const response = await instance.acquireTokenSilent({
        scopes: loginRequest.scopes,
        account: accounts[0]
      });
      return response.accessToken;
    } catch (error) {
      const response = await instance.acquireTokenPopup(loginRequest);
      return response.accessToken;
    }
  };

  const loadUsers = async () => {
    try {
      const token = await getAccessToken();
      const response = await fetch('http://localhost:8000/api/users/', {
        headers: { 'Authorization': `Bearer ${token}` }
      });
      
      if (response.ok) {
        const data = await response.json();
        setUsers(data);
      } else {
        console.error('Failed to load users:', response.status);
      }
    } catch (error) {
      console.error('Error loading users:', error);
    } finally {
      setLoading(false);
    }
  };

  const loadRoles = async () => {
    try {
      const token = await getAccessToken();
      const response = await fetch('http://localhost:8000/api/users/roles', {
        headers: { 'Authorization': `Bearer ${token}` }
      });
      
      if (response.ok) {
        const data = await response.json();
        setRoles(data);
      }
    } catch (error) {
      console.error('Error loading roles:', error);
    }
  };

  const handleRoleChange = async (userId, newRole) => {
    if (!window.confirm('Are you sure you want to change this user\'s role?')) return;
    
    try {
      const token = await getAccessToken();
      const response = await fetch(`http://localhost:8000/api/users/${userId}/role`, {
        method: 'PUT',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ role: newRole })
      });
      
      if (response.ok) {
        await loadUsers();
        alert('User role updated successfully');
      } else {
        const error = await response.json();
        alert(error.detail || 'Failed to update role');
      }
    } catch (error) {
      console.error('Error:', error);
      alert('Failed to update role');
    }
  };

  const handleToggleStatus = async (userId, currentStatus) => {
    const action = currentStatus ? 'disable' : 'enable';
    if (!window.confirm(`Are you sure you want to ${action} this user?`)) return;
    
    try {
      const token = await getAccessToken();
      const response = await fetch(`http://localhost:8000/api/users/${userId}/status`, {
        method: 'PUT',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ is_active: !currentStatus })
      });
      
      if (response.ok) {
        await loadUsers();
        alert(`User ${action}d successfully`);
      } else {
        const error = await response.json();
        alert(error.detail || `Failed to ${action} user`);
      }
    } catch (error) {
      console.error('Error:', error);
      alert(`Failed to ${action} user`);
    }
  };

  const handleDeleteUser = async (userId) => {
    if (!window.confirm('Are you sure you want to delete this user? This action cannot be undone.')) return;
    
    try {
      const token = await getAccessToken();
      const response = await fetch(`http://localhost:8000/api/users/${userId}`, {
        method: 'DELETE',
        headers: { 'Authorization': `Bearer ${token}` }
      });
      
      if (response.ok) {
        await loadUsers();
        alert('User deleted successfully');
      } else {
        const error = await response.json();
        alert(error.detail || 'Failed to delete user');
      }
    } catch (error) {
      console.error('Error:', error);
      alert('Failed to delete user');
    }
  };

  if (loading) {
    return <div className="user-management-loading">Loading users...</div>;
  }

  return (
    <div className="user-management">
      <div className="user-management-header">
        <h2>User Management</h2>
        <p>Manage user roles and permissions</p>
      </div>

      <div className="users-table-container">
        <table className="users-table">
          <thead>
            <tr>
              <th>Name</th>
              <th>Email</th>
              <th>Role</th>
              <th>Status</th>
              <th>Last Login</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {users.map(user => (
              <tr key={user.id} className={!user.is_active ? 'inactive-user' : ''}>
                <td>{user.name || 'N/A'}</td>
                <td>{user.email}</td>
                <td>
                  <select
                    value={user.role}
                    onChange={(e) => handleRoleChange(user.id, e.target.value)}
                    className="role-select"
                  >
                    {roles.map(role => (
                      <option key={role.role_name} value={role.role_name}>
                        {role.display_name}
                      </option>
                    ))}
                  </select>
                </td>
                <td>
                  <span className={`status-badge ${user.is_active ? 'active' : 'inactive'}`}>
                    {user.is_active ? 'Active' : 'Inactive'}
                  </span>
                </td>
                <td>{user.last_login ? new Date(user.last_login).toLocaleString() : 'Never'}</td>
                <td>
                  <div className="action-buttons">
                    <button
                      className={`btn-small ${user.is_active ? 'btn-warning' : 'btn-success'}`}
                      onClick={() => handleToggleStatus(user.id, user.is_active)}
                      title={user.is_active ? 'Disable user' : 'Enable user'}
                    >
                      {user.is_active ? 'Disable' : 'Enable'}
                    </button>
                    <button
                      className="btn-small btn-danger"
                      onClick={() => handleDeleteUser(user.id)}
                      title="Delete user"
                    >
                      Delete
                    </button>
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>

        {users.length === 0 && (
          <div className="no-users">No users found</div>
        )}
      </div>

      <div className="role-info-section">
        <h3>Role Permissions</h3>
        <div className="roles-grid">
          {roles.map(role => (
            <div key={role.role_name} className="role-card">
              <h4>{role.display_name}</h4>
              <p>{role.description}</p>
              <div className="permissions-list">
                {role.can_manage_users && <span className="permission-badge">Manage Users</span>}
                {role.can_add_urls && <span className="permission-badge">Add URLs</span>}
                {role.can_edit_urls && <span className="permission-badge">Edit URLs</span>}
                {role.can_delete_urls && <span className="permission-badge">Delete URLs</span>}
                {role.can_add_servers && <span className="permission-badge">Add Servers</span>}
                {role.can_edit_servers && <span className="permission-badge">Edit Servers</span>}
                {role.can_delete_servers && <span className="permission-badge">Delete Servers</span>}
                {role.can_manage_email_alerts && <span className="permission-badge">Manage Alerts</span>}
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}

export default UserManagement;

