import React, { useState, useEffect } from 'react';
import { getGPUMetrics } from '../services/api';
import AddGPUServerModal from './AddGPUServerModal';
import '../styles/components/GPUStats.css';

function GPUStats() {
  const [gpuMetrics, setGpuMetrics] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [editServer, setEditServer] = useState(null);

  useEffect(() => {
    loadGPUMetrics();
    // Refresh metrics every 30 seconds
    const interval = setInterval(loadGPUMetrics, 30000);
    return () => clearInterval(interval);
  }, []);

  const loadGPUMetrics = async () => {
    try {
      setLoading(true);
      const response = await getGPUMetrics();
      setGpuMetrics(response.data);
      setError(null);
    } catch (error) {
      console.error('Error loading GPU metrics:', error);
      setError('Failed to load GPU metrics');
    } finally {
      setLoading(false);
    }
  };

  const formatBytes = (mib) => {
    if (mib >= 1024) {
      return `${(mib / 1024).toFixed(1)} GiB`;
    }
    return `${mib} MiB`;
  };

  const getUsagePercentage = (used, total) => {
    return ((used / total) * 100).toFixed(1);
  };

  const getProgressColor = (percentage) => {
    const percent = parseFloat(percentage);
    if (percent <= 50) {
      return '#10b981'; // Green
    } else if (percent <= 89) {
      return '#f59e0b'; // Orange
    } else {
      return '#ef4444'; // Red
    }
  };

  // Group metrics by GPU name
  const groupedMetrics = gpuMetrics.reduce((acc, metric) => {
    if (!acc[metric.gpu_name]) {
      acc[metric.gpu_name] = [];
    }
    acc[metric.gpu_name].push(metric);
    return acc;
  }, {});

  if (loading && gpuMetrics.length === 0) {
    return (
      <div className="loading-state">
        Loading GPU metrics...
      </div>
    );
  }

  if (error) {
    return (
      <div className="error-state">
        {error}
      </div>
    );
  }

  if (gpuMetrics.length === 0) {
    return (
      <div className="empty-state">
        <div className="empty-title">No GPU Metrics Available</div>
        <div className="empty-text">No GPU metrics data found in the database</div>
      </div>
    );
  }

  return (
    <div className="gpu-stats-container">
      {Object.keys(groupedMetrics).map(gpuName => (
        <div key={gpuName} className="host-section">
          <h2 className="host-title">{gpuName}</h2>
          <div className="gpu-list">
            {groupedMetrics[gpuName].map((metric) => {
              const memUsagePercent = getUsagePercentage(metric.gpu_memory_used_mib, metric.gpu_memory_total_mib);
              
              return (
                <div key={`${metric.host}-${metric.gpu_index}`} className="gpu-item">
                  <div className="gpu-main-row">
                    <div className="gpu-index-section">
                      <span className="gpu-index-label">GPU Index: </span>
                      <span className="gpu-index-value">{metric.gpu_index}</span>
                    </div>
                    <div className="gpu-memory-section">
                      <span className="memory-label">Total Space: </span>
                      <span className="memory-value">{formatBytes(metric.gpu_memory_total_mib)}</span>
                      <span className="memory-label ml-2">Space Used: </span>
                      <div className="progress-bar-inline">
                        <div
                          className="progress-fill"
                          style={{
                            width: `${memUsagePercent}%`,
                            backgroundColor: getProgressColor(memUsagePercent)
                          }}
                        ></div>
                      </div>
                      <span className="memory-used-value">{formatBytes(metric.gpu_memory_used_mib)}</span>
                      <span className="memory-percentage">({memUsagePercent}%)</span>
                    </div>
                    <div className="gpu-extra-info">
                      <button className="info-btn" title={`Host: ${metric.host}\nGPU Utilization: ${metric.gpu_utilization_pct}%\nFree Memory: ${formatBytes(metric.gpu_memory_free_mib)}\nHost Memory: ${formatBytes(metric.host_memory_used_mib)} / ${formatBytes(metric.host_memory_total_mib)}\nLast Updated: ${new Date(metric.timestamp).toLocaleString()}`}>
                        <svg className="info-icon" viewBox="0 0 20 20" fill="currentColor">
                          <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z" clipRule="evenodd" />
                        </svg>
                      </button>
                    </div>
                  </div>

                  {metric.processes && metric.processes.length > 0 && (
                    <div className="processes-row">
                      {metric.processes.map((process, idx) => {
                        const processMemPercent = getUsagePercentage(process.used_mem_mib, metric.gpu_memory_total_mib);
                        return (
                          <div key={idx} className="process-compact">
                            <span className="process-label">PID: </span>
                            <span className="process-value">{process.pid}</span>
                            <div className="process-progress-bar">
                              <div
                                className="process-progress-fill"
                                style={{
                                  width: `${processMemPercent}%`,
                                  backgroundColor: getProgressColor(processMemPercent)
                                }}
                              ></div>
                            </div>
                            <span className="process-mem">{formatBytes(process.used_mem_mib)}</span>
                            <button 
                              className="info-btn-small" 
                              title={`Process: ${process.process_name}\nCommand: ${process.cmd}\nMemory Used: ${formatBytes(process.used_mem_mib)}\nHost: ${metric.host}\nGPU Index: ${metric.gpu_index}`}
                            >
                              <svg className="info-icon-small" viewBox="0 0 20 20" fill="currentColor">
                                <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z" clipRule="evenodd" />
                              </svg>
                            </button>
                          </div>
                        );
                      })}
                    </div>
                  )}
                </div>
              );
            })}
          </div>
        </div>
      ))}
    </div>
  );
}

export default GPUStats;

